<template>
  <div id="app">
    <h3 class="header">今日头条</h3>
    <!--导航-->
    <div class="nav_list">
      <div id="nav">
        <div
          v-for="i in arr"
          :key="i.title"
          @click="ext=i.type"
          :style="{color:ext==i.type?'red':''}"
        >{{i.title}}</div>
      </div>
    </div>
    <!--内容-->
    <div id="content" ref="wrapper">
      <mt-loadmore :top-method="loadTop" ref="loadmore">
        <ul
          v-infinite-scroll="loadMore"
          infinite-scroll-disabled="loading"
          infinite-scroll-distance="10"
        >
          <li v-for="i in $store.state.newsList" :key="i.id">
            <cont :list="i"></cont>
          </li>
          <div style="text-align:center;background:#fff" v-if="loading">
            <p style="font-size:24px">加载中。。。</p>
            <mt-spinner :type="3" :size="60" style="display:inline-block"></mt-spinner>
          </div>
        </ul>
      </mt-loadmore>
    </div>
    <div class="top" ref="top">
      <a href="#">TOP</a>
    </div>
  </div>
</template>

<script>
import cont from "./components/cont";
export default {
  components: {
    cont
  },
  data() {
    return {
      num: 0,
      loading: false,
      currentPage: 0,
      ext: "astro",
      type: true,
      arr: [
        { type: "astro", title: "推荐" },
        { type: "cul", title: "文化" },
        { type: "edu", title: "教育" },
        { type: "house", title: "房产" },
        { type: "sports", title: "体育" },
        { type: "tech", title: "科技" },
        { type: "games", title: "游戏" },
        { type: "finance", title: "财经" },
        { type: "ent", title: "娱乐" },
        { type: "auto", title: "汽车" }
      ]
    };
  },
  methods: {
    // 下拉刷新
    loadTop() {
      this.type = false;
      this.num++;
      this.$store.commit("getNum", this.num);
      this.currentPage = 0;
      this.$store.commit("getPage", this.currentPage);
    },
    // 上拉加载
    loadMore() {
      this.loading = true;
      this.currentPage++;
      this.$store.commit("getPage", this.currentPage);
    }
  },
  watch: {
    "$store.state.json": {
      handler(a) {
        this.$http
          .jsonp(
            `https://pacaio.match.qq.com/irs/rcd?cid=56&ext=${a.ext}&token=c786875b8e04da17b24ea5e332745e0f&num=10&expIds=20190106A13PFT%7C20190108A04MLS&page=${a.currentPage}`
          )
          .then(data => {
            // console.log(data.data.data);
            this.$store.commit("getList", data.data.data);
            this.loading = false;
            this.$refs.loadmore.onTopLoaded();
          });
      },
      deep: true
    },
    ext(a) {
      this.$store.commit("getExt", a);
      this.currentPage = 0;
      this.$store.commit("getPage", this.currentPage);
    }
  },
  mounted() {
    onscroll = () => {
      if (window.scrollY >= 1000) {
        this.$refs.top.style.display = "block";
      } else {
        this.$refs.top.style.display = "none";
      }
    };
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.header {
  font-size: 1rem;
  text-align: center;
  background: red;
  padding: 20px 0;
}
.nav_list::-webkit-scrollbar {
  display: none;
}
.nav_list {
  overflow-x: auto;
}
#nav {
  width: 160%;
  border-bottom: 1px solid #ccc;
  line-height: 100px;
}
#nav > div {
  font-size: 50px;
  padding: 20px 30px;
  display: inline-block;
}
#content {
  background: #ccc;
}
.mint-loadmore-top {
  height: 100px;
  margin-top: -100px;
}
.mint-loadmore-text {
  font-size: 80px;
}
.top {
  position: fixed;
  bottom: 50px;
  right: 50px;
  font-size: 40px;
  text-align: center;
  display: none;
}
.top a {
  display: block;
  text-decoration: none;
  color: red;
  width: 100px;
  line-height: 100px;
  height: 100px;
  border: 1px solid #000;
  border-radius: 50%;
}
</style>
