<template>
  <div class="hello">
    <div class="title">{{json.title}}</div>
    <div class="titImgs">
      <div v-for="j in json.multi_imgs" :key="j.id">
        <img :src="j"  v-lazy="j" alt />
      </div>
    </div>
    <div class="upDate">{{timeAgo}}</div>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      json: this.list
    };
  },
  computed: {
    timeAgo() {
      var oldTime = new Date(this.json.publish_time).getTime();
      var mistiming = Math.round(new Date()) - oldTime;
      var arrr = ["年", "个月", "星期", "天", "小时", "分钟", "秒"];
      var arrn = [31536000, 2592000, 604800, 86400, 3600, 60, 1];
      for (var i = 6; i >= 0; i--) {
        var inm = Math.floor(mistiming / 1000 / arrn[i]);
        if (inm != 0) {
          var result = inm + arrr[i] + "前";
        }
      }
      return result;
    }
  }
};
</script>

