import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    json: {
      ext: 'astro',
      currentPage: -1,
      num: 0
    },
    newsList: []
  },
  mutations: {
    getNum(state, num) {
      state.json.num = num
    },
    getPage(state, page) {
      state.json.currentPage = page;
    },
    getExt(state, ext) {
      state.json.ext = ext;
    },
    getList(state, j) {
      if (state.json.currentPage == 0) {
        state.newsList = j;
      } else {
        for (var i = 0; i < j.length; i++) {
          state.newsList.push(j[i]);
        }
      }
    },
  },
  actions: {},
  modules: {}
})